
  .c-tabs-nav  {
    font-size: 14px;
    font-size: .875rem;
    margin: 0 auto;
    max-width: 550px;
    position: relative;
    text-transform: uppercase;
    --color-base--hover: var(--hrm_palette-blue-purple);
    --color-tab--base--hover: var(--hrm_palette-blue-purple);

  }
  
  
  
  .center_Tab_Nav_Items {
    border-bottom: 2px solid #e0e0e0;
    display: -ms-flexbox;    
    display: -webkit-box;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    list-style: none;
    padding: 0;
  }
  
  .center_Tab_Nav_Item {
    display: block;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    cursor: pointer;
    outline: none;
    text-decoration: none;
  }
  
  
  .center_Tab_Nav_Item.is-active .center_Tab_Nav_Link {
    color: var(--color-base--hover);
    outline: none;
  }
  
  
  .center_Tab_Nav_Item img, .center_Tab_Nav_Item svg {
    display: block;
    margin: 0 auto;   
    fill: var(--tab--nav-icon-color--);
  }
  
  .center_Tab_Nav_Item.is-active .center_Tab_Nav_Link svg{
    fill: var(--color-base--hover);
  }
  
  .center_Tab_Nav_Item img, .center_Tab_Nav_Item svg {
    display: block;
    margin: 0 auto;   
    fill: var(--tab--nav-icon-color--);
  }
  
  
  svg:not(:root) {
    overflow: hidden;
  }
  
  
  
  .center_Tab_Nav_Link {
    color: var(--icon--color_);
    display: block;
    font-weight: 700;
    padding: 18px 0;
    text-align: center;
    -webkit-transition: color 100ms cubic-bezier(0.25, 0.1, 0.25, 1);
    transition: color 100ms cubic-bezier(0.25, 0.1, 0.25, 1);
    width: 100%;
    outline: none;
    color: #6f6f6f;
    font: 700 14px/19px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;
  }
  
  .c-tabs-nav__indicator {
    background: var(--color-tab--base--hover);
    bottom: 0;
    display: block;
    height: 4px;
    left: 0;
    position: absolute;
    -webkit-transition: left, width;
    transition: left, width;
    -webkit-transition-duration: 300ms;
    transition-duration: 300ms;
    -webkit-transition-timing-function: cubic-bezier(0.25, 0.1, 0.25, 1);
    transition-timing-function: cubic-bezier(0.25, 0.1, 0.25, 1);
  }
  
  
  .text-description{
    padding: 15px 8px;
    font: 600 14px/19px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;
  }
  
  
  
  .white-backColor{
     background-color: #f9f9f9;
     padding: 45px 8px;
  }
